function ModalReducer(state, action) {
    switch (action.type) {
        //  
        case 'COLOR':
            return {...state, color: action.value};
        //  
        case 'TODO':
            return {...state, todo: action.value};
        //  
        case 'CHECK':
            return {...state, checked: !action.value};
        //  
        case 'INITIALIZATION':
            return{
                color: '',
                todo: '',
                todos: '',
                checked: false,
            }
        //      
        case 'TODOS':
            return {...state, date: action.value}
    }
}

export default ModalReducer