import React from 'react';

function calendarReducer(state, action) {

    switch (action.type) {
        case 'INCREMENT':
            if (state.month < 11) {
                return {...state, "month" : state.month+1}
            }
            // 12   Year + 1
            else {
                return {...state, "year": state.year + 1,"month": 0}
            }
        case 'DECREMENT':
            if (state.month > 0) {
                return { ...state, "month": state.month - 1}
            }
            // 1    Year - 1
            else {
                return { ...state, "year": state.year - 1, "month": 11}
            }
        case 'MODAL':
            return {...state, modal: {
                    ...state.modal,
                    visible: !state.modal.visible,
                    index: action.value
                }}
        case 'INSERT':
            //      
            if (state.schedule[action.index] !== undefined) {
                return {...state, schedule: {
                        ...state.schedule,
                        [action.index]: [
                            ...state.schedule[action.index],
                            [action.todo, action.color]
                        ]
                    }}
            }
            //     
            else {
                return {...state, schedule: {
                        ...state.schedule,
                        [action.index]: [[action.todo, action.color]]
                    }}
            }
    }
}

export default calendarReducer;
